import Swal from "sweetalert2"
import { BASE_URL, CLOUDPATH } from "../../../../../../../actions/api";
import { generateAppSecretProof, getFbCode, getTwitterCode } from "../../../../../../../utils/auth";
import axios from "axios";
import { FACEBOOK, GET_FB_USER_DATA, GET_INSTA_USERDATA, INSTAGRAM } from "../../../../../../../actions/socialUrl";
import { ADD_FEED_ACCOUNT } from "../../../../../../../actions/addFeedActions";
import { GET_LOCAL } from "../../../../../../../utils";

export const showAddAccounntAlert = (id) => {
    localStorage.setItem("networkId", id)
    const networkDetails = getNetworkNameById(id);
    const networkName = networkDetails?.networkName || "";
    Swal.fire({
        imageUrl: `${CLOUDPATH}taggshop/app/assets/media/square-icons/${networkName.toLowerCase() || ""}.svg?ver=1`,
        title: `<strong>${networkName}</strong>`,
        html: `Connect to ${networkName} to pull the content for this feed.`,
        showCloseButton: true,
        showCancelButton: false,
        focusConfirm: true,
        confirmButtonText: `Continue with ${networkName}`,
        confirmButtonAriaLabel: `Continue with ${networkName}`,
        cancelButtonText: '<i class="fa fa-thumbs-down"></i>',
        cancelButtonAriaLabel: 'Thumbs down'
    }).then((result) => {
        if (result.value) {
            const networkId = parseInt(id);
            switch (networkId) {
                case 1:
                    getTwitterCode();
                    break;
                case 2:
                    const url = `https://api.instagram.com/oauth/authorize?client_id=480810795949098&redirect_uri=${BASE_URL}/feeds/authCallback/&scope=user_profile,user_media&response_type=code`;
                    window.open(url, 'FbAuth', 'width=500,height=600');
                    break;
                case 18:
                    getFbCode(18);
                    break;
                case 3:
                    getFbCode(3);
                    break;
                default:
                    break;
            }
        } else if (result.dismiss === "cancel") { }
    });
}

const networksMap = {
    1: { networkName: "Twitter", networkCode: "twitter" },
    2: { networkName: "Instagram", networkCode: "instagram" },
    3: { networkName: "Facebook", networkCode: "facebook" },
    4: { networkName: "Google Reviews", networkCode: "google" },
    5: { networkName: "Pinterest", networkCode: "pinterest" },
    6: { networkName: "Flickr", networkCode: "flickr" },
    7: { networkName: "YouTube", networkCode: "youtube" },
    8: { networkName: "Vimeo", networkCode: "vimeo" },
    18: { networkName: "Instagram", networkCode: "instagram-business" },
    23: { networkName: "Airbnb Reviews", networkCode: "airbnb" },
    28: { networkName: "TikTok", networkCode: "tiktok" },
    29: { networkName: "SnapUp", networkCode: "onsiteupload" },
    33: { networkName: "Collab with Creators", networkCode: "collab-with-creators" }
};

export const getNetworkNameById = (id) => networksMap[id];


export const addAccountFromId = async (networkId, code, navigation) => {
        const ugcVerify = GET_LOCAL('ugcVerify');
        console.log('ugcVerify', ugcVerify)
        switch (parseInt(networkId)) {
        // case 1:
        //     await addTwitterAccount(code, updateAccounts);
        //     break;
        case 2:
            await INSTAGRAM_AUTH(code, navigation);
            break;
        case 18:
            if(ugcVerify) await INSTAGRAM_AUTH(code, navigation);
            else await FB_AUTH(code, networkId, navigation)
            break;
        case 3:
            await FB_AUTH(code, networkId, navigation);
            break;
        // case 8:
        //     await addVimeoAccount(code, updateAccounts);
        //     break;
        // case 4:
        //     await addGoogleAccount(code, updateAccounts);
        //     break;
        default:
            break;
    }
}

export const INSTAGRAM_AUTH = async (code, navigation) => {
    console.log('INSTAGRAM_AUTH')
    try {
        const ugcVerify = GET_LOCAL('ugcVerify');
        const res = await INSTAGRAM(code);
        const { access_token } = res.data;
        // const accObj = {
        //     token: access_token || "",
        //     networkId: 2,
        //     validity: 34656,
        //     wallId: localStorage.getItem('wallId')
        // };

        // await ADD_FEED_ACCOUNT(accObj);

        const data = await GET_INSTA_USERDATA(access_token);
        localStorage.setItem('instagram_ugc', JSON.stringify({ ...data, networkId: 18 }));

        if (ugcVerify) {
            if(String(data.username) !== String(localStorage.getItem('ugc_account_check'))){
                navigation('/ugc_right/authCallback');
            }
            localStorage.removeItem('ugcVerify');
        }
    } catch (error) {
        console.error("Error adding Instagram Account:", error);
    }
}

export const FB_AUTH = async (code, networkId, navigation) => {
    console.log('FB_AUTH')
    try {
        const ugcVerify = GET_LOCAL('ugcVerify');
        const { data } = await FACEBOOK(code);
        const appSecretProof = generateAppSecretProof(data.access_token)
        const res = await GET_FB_USER_DATA(data.access_token, appSecretProof);
        localStorage.setItem('instagram_ugc', JSON.stringify({ ...res, networkId }));

        if (ugcVerify && parseInt(localStorage.getItem('userId')) !== parseInt(res.id)) {
            navigation('/ugc_right/authCallback');
        }

        localStorage.removeItem('ugcVerify');
    } catch (error) {
        console.error("Error during Facebook authentication:", error);
    }
}
